<template>
    <div id="blogdetail">
      <Top></Top>
      <!---->
      <div class="main">

        <div class="title">
          <div>
            <img src="../assets/img/vip_p.png" alt="">
          </div>
          <div>NBA</div>
          <div>
            <button></button>
          </div>
        </div>

        <div class="time_info">
          <div>2019/08/05 23:56:12</div>
          <div>7899</div>
        </div>

        <!---->
        <div class="rice_content">
          <p style="line-height: 0.7rem;text-indent: 0.8rem;padding-bottom: 0.5rem;">71516</p>
          <p style="line-height: 0.7rem;text-indent: 0.8rem;padding-bottom: 0.5rem;"></p>
          <p style="line-height: 0.7rem;text-indent: 0.8rem;padding-bottom: 0.5rem;"></p>
          <p style="line-height: 0.7rem;text-indent: 0.8rem;padding-bottom: 0.5rem;"></p>
        </div>

        <div class="buy">
          <div>18:00PR</div>
          <div>D50</div>
          <button></button>
        </div>

      </div>
    </div>
</template>

<script>
  import Top from "../components/Top"
    export default {
        name: "blog-detail",
        components:{
          Top,
        },
      mounted(){
        // 
        document.querySelector('body').addEventListener('touchmove', function (event) {
          event.preventDefault()
        },{ passive: false });
        // 
        document.querySelector('body').addEventListener('mousewheel', function (event) {
          event.preventDefault()
        },{ passive: false })
      }
    }
</script>

<style scoped>
  .main .title {
    display: flex;
    border-bottom: 1px solid #f2f2f2;
    padding: 0.2rem;
    margin: 0.3rem;
  }
  .main .title div:first-child img {
    height: 1rem;
    width: 1rem;
    border-radius: 1rem;
    margin-right: 0.2rem;
  }
  .main .title div:nth-child(2) {
    flex: 1;
  }
  .main .title div:nth-child(3) button {
    border-radius: 0.1rem;
    background: #c262d5;
    color: white;
    line-height: 0.6rem;
    border: 0;
    font-size: 0.3rem;
    padding: 0.1rem 0.3rem;
  }
  .main .time_info {
    text-align: center;
    color: grey;
    line-height: 0.7rem;
  }
  .main .rice_content {
    padding: 0.5rem;
  }
  .main .buy {
    width: 100%;
    max-width: 500px;
    position: fixed;
    bottom: 0;
    left: 50%;
    margin-left: -5rem;
    text-align: center;
    background: white;
    padding-top: 0.5rem;
    padding-bottom: 0.3rem;
  }
  .main .buy div:nth-child(2) {
    margin-top: 2rem;
  }
  .main .buy button {
    background: #c262d5;
    color: white;
    border: 0;
    border-radius: 0.1rem;
    line-height: 0.9rem;
    width: 8.5rem;
    margin: 0.3rem;
  }

</style>



