<template>
    <div id="vipdetail">
      <Top></Top>
      <!---->
      <div class="main">

        <div class="title">
          <div>
            <img src="../assets/img/vip_p.png" alt="">
          </div>
          <div>
            <div class="name"></div>
            <div class="price">1000/&nbsp;&nbsp;&nbsp;&nbsp;5000/&nbsp;&nbsp;&nbsp;&nbsp;15000/</div>
          </div>
        </div>

        <!---->
        <div class="rich_content">

          <p style="line-height: 0.7rem;text-indent: 0.8rem;padding-bottom: 0.5rem;">71516</p>
          <p style="line-height: 0.7rem;text-indent: 0.8rem;padding-bottom: 0.5rem;"></p>
          <p style="line-height: 0.7rem;text-indent: 0.8rem;padding-bottom: 0.5rem;"></p>
          <p style="line-height: 0.7rem;text-indent: 0.8rem;padding-bottom: 0.5rem;"></p>

        </div>

        <!---->
        <div class="require">

          <!---->
          <div class="content">
            <div class="score">
              <div>5
                <span style="color:black;">D</span>
                <span style="color:#c262d5;">W</span>
                <span style="color:#c262d5;">W</span>
                <span style="color:green;">L</span>
                <span style="color:#c262d5;">W</span>
              </div>
              <div @click="toMore">></div>
            </div>
            <div class="buy">
              <button>1000/</button>
              <button>5000/</button>
              <button>15000/</button>
            </div>
          </div>


        </div>

      </div>
    </div>
</template>

<script>
  import Top from "./Top"
    export default {
        name: "vip-detail",
      components:{
          Top,
      },
      methods:{
        toMore:function () {
          this.$router.push({name:'more_record'})
        }
      },
      mounted(){
        // 
        document.querySelector('body').addEventListener('touchmove', function (event) {
          event.preventDefault()
        },{ passive: false });
        // 
        document.querySelector('body').addEventListener('mousewheel', function (event) {
          event.preventDefault()
        },{ passive: false })
      }
    }
</script>

<style scoped>
  .main .title {
    display: flex;
    padding: 0.5rem;
  }
  .main .title div:first-child img {
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 0.3rem;
  }
  .main .title div:last-child {
    flex: 1;
  }
  .main .title div:last-child div {
    line-height: 0.7rem;
    font-family: w5, '';
  }
  .main .title div:last-child div:first-child {
    font-size: 0.35rem;
    color: black;
  }
  .main .title div:last-child div:last-child {
    font-size: 0.3rem;
    color: grey;
  }
  .main .rich_content {
    padding: 0.5rem;
  }
  .main .require .content {
    position: fixed;
    bottom: 0;
    left: 50%;
    margin-left: -5rem;
    width: 100%;
    max-width: 500px;
    z-index: 100;
  }
  .main .require .content .score {
    display: flex;
    background: #f1f1f1;
    padding: 0.3rem 0.5rem;
  }
  .main .require .content .score div:first-child {
    flex: 1;
  }
  .main .require .content .score div:last-child {
    color: grey;
    font-size: 0.3rem;
  }
  .main .require .content .buy {
    text-align: center;
    background: white;
    padding: 0.3rem;
  }
  .main .require .content .buy button {
    width: 8.5rem;
    line-height: 1rem;
    background: #c262d5;
    color: white;
    border: 0;
    border-radius: 0;
    margin: 0.2rem;
  }
  .main .require .content .buy button:nth-child(2) {
    background: #865cd4;
  }
  .main .require .content .buy button:nth-child(3) {
    background: #751a88;
  }

</style>



