<template>
    <div id="personpush">
      <!---->
      <div class="main">
        <PersonNav v-bind:person_nav_index="3"></PersonNav>

        <div class="content">
          <div class="input">
            <div class="name"></div>
            <div class="value">
              <input type="text" placeholder="">
            </div>
          </div>
          <div>
            
            <input class="checkbox" type="checkbox">
          </div>
          <div class="input">
            <div class="name"></div>
            <div class="value">
              <textarea placeholder="" rows="10"></textarea>
            </div>
          </div>
          <div class="input">
            <div class="name"></div>
            <div class="value">
              <span id="date_picker" onclick="datepicker(this)"></span>
            </div>
          </div>
          <div class="input">
            <div class="name"></div>
            <div class="value">
              <input type="text" placeholder="">
            </div>
          </div>

          <div class="button">
            <button></button>
          </div>

        </div>



      </div>
    </div>
</template>

<script>
  import PersonNav from "./PersonNav"
    export default {
        name: "person-push",
        components:{
          PersonNav,
        },
      mounted(){

        var year,month,day;
        mui.init();

        // 
        var get_date = function (){
          var date = new Date();
          year =  date.getFullYear();
          month = date.getMonth()+1;
          month = month<10?'0'+month:month;
          day = date.getDate();
          day = day<10?'0'+day:day;
          var current_date = year+"-"+month+"-"+day;
          return current_date;
        }

        mui.ready(function(){
          document.getElementById("date_picker").innerHTML = get_date();
        });



        // 
        document.querySelector('#date_picker').addEventListener('click', function () {
          var obj = document.querySelector('#date_picker');
          var dtpicker = new mui.DtPicker({
            type: "date",//
            beginDate: new Date(2000, 1, 1),//
            endDate: new Date(year, month-1, day)//
          });
          dtpicker.setSelectedValue(get_date());
          dtpicker.show(function(e) {
            obj.innerHTML = e;
            user_birth = e;
          })
        });



      }
    }
</script>

<style scoped>
  .main .content {
    margin: 0.5rem;
  }
  .main .content .checkbox {
    height: 0.5rem;
    width: 0.5rem;
    position: relative;
    top: 0.15rem;
  }
  .main .content .input {
    display: flex;
    margin: 0.3rem 0;
  }
  .main .content .input .name {
    width: 2rem;
  }
  .main .content .input .value {
    flex: 1;
    width: 90%;
    border: 1px solid #f2f2f2;
    font-size: 0.4rem;
    padding: 0.2rem 0.4rem;
  }
  .main .content .input .value input,
  textarea {
    width: 90%;
    font-size: 0.4rem;
    border: 0;
    padding: 0.2rem 0.4rem;
  }
  .main .content .button {
    text-align: center;
  }
  .main .content button {
    width: 8rem;
    height: 1rem;
    background: #c262d5;
    color: white;
    border: 0;
    border-radius: 0.1rem;
    margin: 0.5rem;
  }

</style>



